import React, { useEffect, useState } from 'react';
import './App.scss';
import { GoodsList } from './GoodsList';

import { getAll, get5First, getRedGoods } from './api/goods';
import { Good } from './types/Good';

enum ButtonType {
  All = 'all-button',
  FirstFive = 'first-five-button',
  ColorRed = 'red-button',
}

function getButtonName(type: ButtonType) {
  if (type === ButtonType.FirstFive) {
    return 'Load 5 first goods';
  }

  if (type === ButtonType.ColorRed) {
    return 'Load red goods';
  }

  return 'Load all goods';
}

export const App: React.FC = () => {
  const [goods, setGoods] = useState<Good[]>([]);
  const [type, setType] = useState<ButtonType>(ButtonType.All);

  useEffect(() => {
    switch (type) {
      case ButtonType.FirstFive:
        get5First()
          .then(setGoods);
        break;

      case ButtonType.ColorRed:
        getRedGoods()
          .then(setGoods);
        break;

      default:
        getAll()
          .then(setGoods);
        break;
    }
  }, [type]);

  return (
    <div className="App">
      <h1>Dynamic list of Goods</h1>

      {Object.values(ButtonType).map(button => (
        <button
          key={button}
          type="button"
          data-cy={button}
          onClick={() => setType(button)}
        >
          {getButtonName(button)}
        </button>
      ))}

      <GoodsList goods={goods} />
    </div>
  );
};
